import React from "react";
import axios from "axios";
import Movie from "../components/Movie";
import "./Home.css";

// react hook
class Home extends React.Component {
  
  state = {
    isLoading: true,
    movies: []
  };

  // async ÎπÑÎèôÍ∏∞ÎãàÍπå axios ÎùºÏù¥Î∏åÎü¨Î¶¨Î•º awaitÌï¥Îùº (async, await Ìï®Íªò ÏÇ¨Ïö©)
  getMovies = async () => {
    const {
      data: {
        data: {movies}
      }
    } = await axios.get("https://yts-proxy.now.sh/list_movies.json?sort_by=rating");
    this.setState({ movies, isLoading: false });
  }

  componentDidMount() {
    this.getMovies();
  }

  render() {
    const { isLoading, movies } = this.state;

    return (
      <section className="container">
        { isLoading ? (
          <div className="loader">
            <div class="lds-spinner">
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
              <div></div>
            </div>
          </div>
        ) : (
          <div className="movies">
            { movies.map(movie => (
              <Movie 
                key={movie.id}
                id={movie.id} 
                year={movie.year} 
                title={movie.title} 
                summary={movie.summary} 
                poster={movie.medium_cover_image} 
                genres={movie.genres}
              />
            ))}
          </div>
        )}
      </section>
    );
  }
}

export default Home;