import React from "react";
import { HashRouter, Route } from "react-router-dom";
import Home from "./routes/Home";
import About from "./routes/About";
import Detail from "./routes/Detail";
import Navigation from "./components/Navigation";
import "./App.css";


/*
 * routerÎäî Í∏∞Î≥∏Ï†ÅÏúºÎ°ú URLÏùÑ Í∞ÄÏ†∏ÏôÄ ÌôïÏù∏/ÎπÑÍµêÌïòÍ≥†, 
 * Îß§ÏπòÍ∞Ä ÎêúÎã§Î©¥ Ïö∞Î¶¨Í∞Ä routerÏóêÍ≤å Î¨¥ÏóáÏùÑ Î™ÖÎ†πÌñàÎäîÏßÄÏóê Îî∞Îùº
 * Ìï¥Îãπ componentÎ•º Î∂àÎü¨Ïò¥
 *  <Route path="/home/introduction">: Îëê Í∞úÏùò routerÍ∞Ä Îß§ÏπòÎê®
 * path="/"ÏùºÎïåÎßå(exact={true}) homeÏùÑ Î†åÎçîÎßÅ Ìï¥ Ï§ò = nothing else.
*/
function App() {
  return (
  <HashRouter>
    <Navigation />
    <Route path="/" exact={true} component={Home} />
    <Route path="/about" component={About}/>
    <Route path="/movie/:id" component={Detail} />
  </HashRouter>
  );
}

export default App;